import React, { useState } from "react";
import { Card, Button, Pagination } from "react-bootstrap";

const GridPage = ({ title, data }) => {
  const [page, setPage] = useState(1);
  const itemsPerPage = 6;
  const startIdx = (page - 1) * itemsPerPage;
  const paginated = data.slice(startIdx, startIdx + itemsPerPage);

  const totalPages = Math.ceil(data.length / itemsPerPage);

  return (
    <div className="bg-dark text-light p-4">
      <h2 className="text-center mb-4">{title}</h2>
      <div className="d-flex flex-wrap justify-content-center gap-4">
        {paginated.map((item, idx) => (
          <Card key={idx} style={{ width: "18rem" }} className="bg-secondary text-light">
            <Card.Img variant="top" src={item.img} style={{ height: "250px", objectFit: "contain" }} />
            <Card.Body>
              <Card.Title>{item.nome}</Card.Title>
              <Card.Text>
                Tamanho: {item.tamanho} <br />
                {item.preco ? `Preço: R$ ${item.preco}` : <a href={`https://instagram.com/uplevel3d?text=Olá! Quero saber mais sobre a figura ${item.nome}`} target="_blank" rel="noreferrer">Fale no Instagram</a>}
              </Card.Text>
            </Card.Body>
          </Card>
        ))}
      </div>

      <Pagination className="justify-content-center mt-4">
        {[...Array(totalPages)].map((_, i) => (
          <Pagination.Item key={i + 1} active={i + 1 === page} onClick={() => setPage(i + 1)}>
            {i + 1}
          </Pagination.Item>
        ))}
      </Pagination>
    </div>
  );
};

export default GridPage;
